<template>

    <v-row class="qiong-pl-25 qiong-pr-20">
        <qiong-page-header>
            <slot></slot>
        </qiong-page-header>
        <div class="tiny_space"></div>
        <qiong-page-ling :ex_class="extra_calss"></qiong-page-ling>

        <div :class="space"></div>
    </v-row>
    
</template>

<script>
import QiongPageHeader from '../QiongPageHeader.vue'
import QiongPageLing from '../QiongPageLing.vue'
    export default {
  components: { QiongPageHeader, QiongPageLing },
        name: '',
        props: {
            space: {
                type: String,
                default: 'qiong-pt-30'
            },
            extra_calss: {
                type: String
            }
        },
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.tiny_space
    min-height: 0.5vw
    display: block
    width: 100%
</style>


